
import { defineComponent, ref } from "vue";
export default defineComponent({
  props: {
    title: String,
    path: {
      type: String,
      default: "",
    },
  },
  setup() {
    const isOpen = ref(true);

    function toggleShow() {
      isOpen.value = !isOpen.value;
    }
    return { isOpen, toggleShow };
  },
});
